import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  { path: '/', redirect: '/films' },
  {
    path: '/films',
    name: 'Films',
    redirect: '/films/nowPlaying',
    component: () => import('@/views/Films/index.vue'),

    children: [
      { path: 'nowPlaying', name: 'NowPlaying', component: () => import('@/views/NowPlaying/index.vue'), meta: { isShow: true } },
      { path: 'comingSoon', name: 'ComingSoon', component: () => import('@/views/ComingSoon/index.vue'), meta: { isShow: true } },
    ],
  },
  { path: '/Detail/:id', name: 'Detail', component: () => import('@/views/Detail/index.vue'), props: true, meta: { isShow: false } },
  { path: '/cinemas', name: 'Cinemas', component: () => import('@/views/Cinemas/index.vue'), meta: { isShow: true } },
  { path: '/center', name: 'Center', component: () => import('@/views/Center/index.vue'), meta: { isShow: true } },
  { path: '/city', name: 'City', component: () => import('@/views/City/index.vue'), meta: { isShow: false } },
  { path: '/search', name: 'Search', component: () => import('@/views/Search/index.vue'), meta: { isShow: false } }
]

const router = new VueRouter({
  routes,
})

export default router
