import request from './request'
// js中引用vuex
import store from '@/store/index.js'
// console.log(store.state);
// 获取轮播图
const GetSwiper = () =>
  request.get(`/gateway?type=2&cityId=${store.state.cityId}&k=3129552`, {
    headers: {
      'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.2.0","e":"16171726233269981940744193","bc":"310100"}',
      'X-Host': 'mall.cfg.common-banner',
    },
  })

// 获取即将上映的电影
const GetComingSoonMovie = (pageNum) =>
  request.get(`/gateway?cityId=${store.state.cityId}&pageNum=${pageNum}&pageSize=10&type=2&k=6413016`, {
    headers: {
      'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.2.0","e":"16171726233269981940744193","bc":"310100"}',
      'X-Host': 'mall.film-ticket.film.list',
    },
  })

// 获取正在热映的数据
const GetNowPlayingMovie = (pageNum) =>
  request.get(`/gateway?cityId=${store.state.cityId}&pageNum=${pageNum}&pageSize=10&type=1&k=7865452`, {
    headers: {
      'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.2.0","e":"16171726233269981940744193","bc":"310100"}',
      'X-Host': 'mall.film-ticket.film.list',
    },
  })

// 获取详情信息
const GetMoviesDetail = (id) =>
  request.get(`/gateway?filmId=${id}&k=1570310`, {
    headers: {
      'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.2.0","e":"16171726233269981940744193","bc":"310100"}',
      'X-Host': 'mall.film-ticket.film.info',
    },
  })

// 获取电影院列表
const GetMoviesCinemaList = () =>
  request.get(`/gateway?cityId=${store.state.cityId}&ticketFlag=1&k=6094853`, {
    headers: {
      'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.2.0","e":"16171726233269981940744193","bc":"310100"}',
      'X-Host': 'mall.film-ticket.cinema.list',
    },
  })

//获取城市列表
const GetCityList = () =>
  request.get(`/gateway?k=3561897`, {
    headers: {
      'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.2.0","e":"16171726233269981940744193","bc":"310100"}',
      'X-Host': 'mall.film-ticket.city.list',
    },
  })

export default {
  GetSwiper,
  GetComingSoonMovie,
  GetNowPlayingMovie,
  GetMoviesDetail,
  GetMoviesCinemaList,
  GetCityList,
}
