import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate";
Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState()],
  state: {
    cityId:310100,
    cityName:'上海'
  },
  mutations: {
    changeCity(state,value){
      state.cityId=value.id
      state.cityName=value.name
    }
  },
  actions: {
  },
  modules: {
  }
})
